@import '../variables.scss';

.pizza-block-wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.pizza-block {
  margin: 0 auto;
  @media (max-width: 390px) {
    width: 100%;
  }
  text-align: center;
  margin-bottom: 65px;
  @media (max-width: 1000px) {
    margin-bottom: 40px;
  }

  &__image {
    cursor: pointer;
    max-width: 260px;
    max-height: 260px;
    object-fit: cover;
    object-position: center, center;
    @media (max-width: 400px) {
      max-width: 230px;
      max-height: 220px;
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1%;
    margin-bottom: 20px;
  }

  &__selector {
    display: flex;
    background-color: #f3f3f3;
    border-radius: 10px;
    flex-direction: column;
    padding: 6px;

    ul {
      display: flex;
      flex: 1;

      &:first-of-type {
        margin-bottom: 6px;
      }

      li {
        padding: 8px;
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        @include noselect();
        &.active {
          background: #ffffff;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
          border-radius: 5px;
          cursor: auto;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
  }
}
